.weather-forecast {
    position:absolute;
    top:18%;
    left:45%;
  }
  
  .forecast-list {
    display:grid;
    grid-template-columns:auto auto auto;
    flex-wrap: wrap;
    justify-content: center;
    gap: 20px;
  }
  
  .forecast-item {
    display: flex;
    flex-direction: column;
    justify-content: center;
    background:#4793AF;
    border: 1px solid #dddddd;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    padding: 20px;
    width: 200px;
    transition: transform 0.2s;
    align-items: center;
    text-align: center;
  }
  .forecast-item img{
    width:100px;
    height:100px;
  }
  .forecast-item:hover {
    transform: scale(1.05);
  }
  
  .forecast-item h2 {
    font-size:16px;
    margin-bottom: 10px;
    color:white;
  }
  
  .forecast-item p {
    margin: 5px 0;
    font-size: 1em;
    color:white;
  }
  
  @media (max-width: 768px) {
    .forecast-item {
      width: 100%;
    }
  }
  